<script>
import { OKXStarknetProvider } from "@okxconnect/universal-provider";
import { CallData, uint256, RpcProvider, Account } from "starknet";

export default {
  name: "StarknetTransactionPanel",
  methods: {
    async starknet_signMessage() {
      this.address = this.getCurrentAddress()
      if (this.address === "") {
        alert("address can't be ''");
        return
      }

      let signData = this.signData
      this.$emit("generateTransactionParams", JSON.stringify(signData))
      let provider = new OKXStarknetProvider(window.provider)
      let res = await provider.signMessage(this.address, signData, "starknet:mainnet")
      if (res.code) {
        if (res.code == 301) {
          let msg = "Wallet address is registering, please try again later!"
          alert(msg)
          this.$emit("generateTransactionResult", msg, "starknet_signMessage")
        }
        this.$emit("generateTransactionResult", res, "starknet_signMessage")
        return
      }
      console.log(res)

      const rpcProvider = new RpcProvider({ nodeUrl: "https://free-rpc.nethermind.io/mainnet-juno/v0_7" });
      let pk = "0x123"; /// random pk
      const account = new Account(rpcProvider, this.address, pk)
      let that = this
      account.verifyMessage(signData, res).then(function (isValid) {
        that.$emit("generateTransactionResult", "verify:" + isValid + "\n" + res, "starknet_signMessage")
      }).catch(function (error) {
        that.$emit("generateTransactionResult", "failed:" + error + "\n" + res, "starknet_signMessage")
      })
    },

    async starknet_sendTransaction() {
      this.address = this.getCurrentAddress()
      if (this.address === "") {
        alert("address can't be ''");
        return
      }

      let val = uint256.bnToUint256(120000000000000000)
      const transferCalldata = CallData.compile({
        to: "0x00b909cefa36ab6bc26f5887a867e46ef162238f0a171b1c2974b665afd4237f",
        value: val
      })

      // const ETHTokenAddress = "0x049d36570d4e46f48e99674bd3fcc84644ddd6b96f7c741b1562b82f9e004dc7"
      // const STARKTokenAddress = "0x04718f5a0fc34cc1af16a1cdee98ffb20c31f5cd61d6ab07201858f4287c938d"
      const DAITokenAddress = "0x00da114221cb83fa859dbdb4c44beeaa0bb37c7537ad5ae66fe5e0efd20e6eb3"

      const invokeParams = {
        calls: [
          {
            contract_address: DAITokenAddress,
            entry_point: "transfer",
            calldata: transferCalldata
          }
        ],
      }

      this.$emit("generateTransactionParams", JSON.stringify(invokeParams))
      let provider = new OKXStarknetProvider(window.provider)

      let that = this
      provider.sendTransaction(this.address, invokeParams, "starknet:mainnet").then(function(){
        let res = provider.sendTransaction("starknet:mainnet", this.address, invokeParams)
        this.$emit("generateTransactionResult", res, "starknet_sendTransaction")
      }).catch(function(error) {
        if (error.code == 301) {
          let msg = "Wallet address is registering, please try again later!"
          alert(msg)
          that.$emit("generateTransactionResult", msg, "starknet_sendTransaction")
        }else {
          that.$emit("generateTransactionResult", error, "starknet_sendTransaction")
        }
      });
    },

    getCurrentAddress() {
      let provider = new OKXStarknetProvider(window.provider)
      const address = provider.getAccount("starknet:mainnet")?.address
      return address
    },

    getPublicKey() {
      let provider = new OKXStarknetProvider(window.provider)
      const account = provider.getAccount("starknet:mainnet")
      console.log("获取到的account是：")
      console.log(account)
      return "05d8e5e9c1dd686246716c888159271d2f13986cb4acee46164e2c4e034629c0"
    }
  },

  data() {
    const signData = {
        "domain": {
          "chainId": "0x534e5f4d41494e",
          "name": "STRKFarm",
          "version": "1"
        },
        "message": {
          "document": "app.strkfarm.xyz/tnc/v1",
          "message": "Read and Agree T&C"
        },
        "primaryType": "Tnc",
        "types": {
          "StarkNetDomain": [
            {
              "name": "name",
              "type": "felt"
            },
            {
              "name": "version",
              "type": "felt"
            },
            {
              "name": "chainId",
              "type": "felt"
            }
          ],
          "Tnc": [
            {
              "name": "message",
              "type": "felt"
            },
            {
              "name": "document",
              "type": "felt"
            }
          ]
        }
    }

    return {
      signData
    }
  }
}
</script>

<template>
  <el-row :gutter="12" class="button-row">
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="starknet_signMessage()">signMessage</el-button>
      <div style="font-size: 12px;margin-top: 12px; margin-left: 4px; color: gray; text-align: left;">
        <li>
          1. 验签不成功的话，请尝试连接VPN
        </li>
      </div>
    </el-col>
    <div class="line"></div>

    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="starknet_sendTransaction(true)">
        sendTransaction
      </el-button>

      <div style="font-size: 12px;margin-top: 12px; margin-left: 4px; color: gray; text-align: left;">
        <li>
          1. 发送0.12个DAI到0x00b9...4237f的地址
        </li>
        <li>
          2. 请保证你的钱包有足够的DAI
        </li>
      </div>
    </el-col>
  </el-row>
</template>

<style scoped>
.grid-content {
  margin-top: 8px;
  height: 40px;
  width: 100%;
}

.line {
  height: 2px;
  width: 100%;
  margin: 20px 0px;
  background-color: gray;
}
</style>